.svg-wrap {
  position: fixed;
  height: 50px;
  width: 100vw;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  z-index: 99;
}

.svg-home-wrap,
.svg-message-wrap,
.svg-user-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  position: relative;
}

.svg-wrap span {
  font-size: 14px;
  color: #848484;
}
.select path {
  fill: #e86ea9;
}

.select span {
  color: #1d8dcc;
}

.svg-message-wrap .unread-indicator {
  position: absolute;
  top: -5px;
  right: -10px;
}

.svg-message-wrap .unread-indicator span {
  display: inline-block;
  text-align: center;
  line-height: 20px;
  background-color: red;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  color: white;
}
