.header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  background-color: #8f79b8;
  color: white !important;
  font-size: 10px !important;
  z-index: 99;
}

.header .goBack {
  position: absolute;
  top: 7.5px;
  left: 15px;
  cursor: pointer;
}

.header .goBack img {
  width: 35px;
}
