.chat-wrap .chat-receive {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.empty {
  height: 52px;
}

.chat-wrap .chat-send {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.chat-receive img,
.chat-send .msg {
  margin-right: 20px;
}

.chat-receive img,
.chat-send img {
  width: 35px;
  height: 35px;
}
.chat-receive .msg,
.chat-send .msg {
  max-width: 70%;
  padding: 10px 10px;
  background-color: white;
  border-radius: 5px;
}

.chat-receive,
.chat-send {
  margin-top: 10px;
}

.chat-send .msg {
  background-color: #8f79b8;
}
.chat-send p {
  color: white;
}

.chat-send {
  padding-right: 5px;
}

.chat-receive {
  padding-left: 5px;
}

.chat-form-wrap {
  position: fixed;
  left: 0;
  bottom: -218px;
  width: 100%;
  background-color: #dadadb;
  transition: ease 0.5s;
}

.toggleShow {
  bottom: 0px !important;
}

.chat-form {
  display: flex;
  justify-content: stretch;
  align-items: flex-end;
  margin: 5px 8px;
  box-sizing: border-box;
}

.chat-form textarea {
  flex-grow: 1;
  box-sizing: border-box;
  border: 0;
  outline-style: none;
  font-size: 20px;
  padding: 6px 3px;
  height: 35px;
  resize: none;
  max-height: 150px !important;
  border-radius: 5px 5px 5px 5px;
}

.chat-form button {
  height: 35px;
  width: 60px;
  background-color: #8f79b8;
  border: 0;
  font-size: 20px;
  color: white;
  border-radius: 5px 5px 5px 5px;
  outline-style: none;
}

.chat-form .emoji {
  height: 35px;
  width: 35px;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.emoji-collection {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  height: 218px;
  overflow: scroll;
}

.emoji-collection span {
  font-size: 22px;
  text-align: center;
  border: 1px solid #a7a7a7;
  margin-left: -1px;
  margin-top: -1px;
}
