.homepage-wrap {
  width: 100%;
  overflow: hidden;
}

.homepage-wrap .post {
  position: fixed;
  top: 13px;
  right: 15px;
  z-index: 99;
}

.homepage-wrap .refresh {
  position: fixed;
  top: 13px;
  right: 55px;
  z-index: 99;
}

.homepage-wrap .post img,
.homepage-wrap .refresh img {
  width: 24px;
}

.newPost-indicator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.newPost-indicator .indicator-body {
  background-color: #fc7d4c !important;
  padding: 5px 10px;
  margin-top: 10px;
  border-radius: 20px;
}

.newPost-indicator .indicator-body span {
  color: white;
  font-weight: bold;
}

.loadMore {
  text-align: center;
}
