.message-collection .friend {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 78px;
}

.message-collection .friend .friend-img {
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.message-collection .friend .friend-img img {
  width: 48px;
  height: 48px;
}

.message-collection .friend-info {
  padding: 15px 0;
  position: relative;
  padding-left: 10px;
  flex-grow: 1;
  height: 48px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  border-bottom: 1px solid rgba(132, 132, 132, 0.1);
}

.message-collection .friend-time {
  position: absolute;
  right: 10px;
  top: 15px;
  font-size: 12px;
  color: #848484;
}

.message-collection .friend-info-bottom {
  font-size: 15px;
  color: #848484;
}

.message-collection .friend-indicator {
  position: absolute;
  top: -10px;
  right: -10px;
}

.message-collection .friend-indicator span {
  display: inline-block;
  text-align: center;
  line-height: 20px;
  background-color: red;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  color: white;
}
