.profile-content {
  margin: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #bbbbbb;
  position: relative;
}

.profile-top {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.profile-username {
  color: #848484;
}

.profile-left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-right {
  margin-left: 30px;
  height: 53px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.profile-post {
  margin-top: 10px;
  position: relative;
}

.profile-post .post-image .post-origin {
  margin-top: 5px;
  width: 180px;
  height: 240px;
  object-fit: cover;
}

.profile-post .post-image .zoom-pic {
  position: absolute;
  z-index: -99;
}

.zoom-click {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: black;
  animation: zoom-in 0.2s ease forwards;
  z-index: 100;
}

.zoom-click .zoom {
  position: fixed !important;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  animation: zoom-in 0.2s ease forwards;
  height: auto !important;
  object-fit: cover;
}

@keyframes zoom-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.profile-post .post-image .zoom {
  margin-top: 5px;
  width: 180px;
  height: 240px;
  object-fit: cover;
}

.profile-post span {
  color: #848484;
  font-size: 12px;
}

.profile-talk {
  position: absolute;
  top: 20px;
  right: 20px;
}

.profile-chat {
  width: 40px;
}

.post-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.post-more {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  position: relative;
}
.post-info .post-more img {
  width: 20px;
  height: 20px;
}

.post-more-menue {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  background-color: #1d1d1d;
  position: absolute;
  left: -160px;
  top: -5px;
  overflow: hidden;
  transform-origin: right;
  transform: scaleX(0);
}

.post-more-like,
.post-more-comment {
  display: flex;
  justify-content: center;
  align-items: center;
}

.post-more-like span,
.post-more-comment span {
  margin-left: 5px;
}

.post-more-comment img {
  margin-left: 10px;
}

.expand-in {
  animation: expand-in 0.2s ease forwards;
}
.expand-out {
  animation: expand-out 0.2s ease forwards;
}

.post-like-comment .inputComment {
  height: 100px;
  flex-grow: 1;
  margin-right: 5px;
}

.post-like-comment .input-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.comment-button {
  width: 120px !important;
  font-size: 15px !important;
  margin-bottom: 5px !important;
}

.post-like-comment .like {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  background-color: #eeeeee;
}

.post-like-comment .comment {
  margin-top: 5px;
  background-color: #eeeeee;
  overflow: hidden;
}

.comment-collapse {
  max-height: 110px;
}

.comment-expand {
  max-height: 100%;
}

.post-like-comment .comment-list {
  padding: 3px 0;
}
.comment-list p {
  font-size: 15px;
}

.post-like-comment .like img {
  width: 15px;
  height: 15px;
}

.post-like-comment .username,
.post-like-comment .username span:nth-of-type(2n-1) {
  font-size: 15px;
  color: #0167a1 !important;
  cursor: pointer;
}

.post-like-comment .username .reply {
  font-size: 15px !important;
  color: #848484 !important;
}

.like .username {
  margin-left: 5px;
}

.comment-button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hide {
  display: none !important;
}

.replyInput {
  width: 100%;
  height: 40px;
  font-size: 15px;
}

.replyInput::placeholder {
  color: #848484;
}

.replyInput-buttons {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.replyInput-wrapper .replyInput-comment,
.replyInput-wrapper .replyInput-cancel {
  height: 30px;
  padding: 5px 10px;
  font-weight: bold;
  margin-right: 10px;
}

.comment-content {
  margin-top: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.comment-content .profilePhoto {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.comment-content-left {
  margin-left: 10px;
}

.profilePhoto {
  cursor: pointer;
}

@keyframes expand-in {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes expand-out {
  0% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
