.info-wrap {
  width: 100vw;
  height: 100vh;
  padding-top: 10px;
  box-sizing: border-box;
  overflow-x: hidden;
  background: linear-gradient(180deg, #1d8dcc, #8f79b8);
  position: relative;
}
.info-content {
  margin-top: 60px;
  padding-bottom: 50px;
}
.info-content span {
  color: white;
  height: 48px;
  line-height: 48px;
}

.info-profiles {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin-top: 20px;
  background-color: #f6f6f6;
}

.info-realProfile {
  width: 100%;
}

.info-realProfile span {
  display: inline-block;
  height: 48px;
  margin: 15px 0;
}

.info-image {
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid rgba(144, 144, 146, 0.2);
  margin-left: -1px;
  margin-bottom: -1px;
}

.info-image img {
  width: 50%;
  height: auto;
}

.info-image p {
  margin-top: 10px;
  font-size: 10px;
}

.info-gender,
.info-prefer,
.info-age {
  margin-top: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.info-gender input,
.info-prefer input,
.info-age input {
  margin-left: 20px;
}

.info-age input {
  width: 40px;
}

#detail {
  display: block;
  margin: 15px auto 0 auto;
}

.info-detail {
  margin-top: 30px;
}

.info-title {
  display: flex;
  align-items: center;
}

.info-title img {
  margin-left: 20px;
}

.info-save {
  margin-top: 30px;
}

.disable {
  background: grey !important;
}
