.login-wrap {
  height: 100vh;
  width: 800px;
  overflow: scroll;
  box-sizing: border-box;
  padding: 0 20px;
  padding-bottom: 30px;
}

.login-header {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 50px;
  background-color: #8f79b8;
}
.login-header img {
  width: 220px;
  height: 80%;
}

.login-wrap .input {
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
  font-size: 20px;
  padding: 0 20px;
  box-sizing: border-box;
  border-radius: 25px;
  outline: none;
  border: 1px solid white;
  background: linear-gradient(90deg, #1d8dcc, #8f79b8);
  color: white;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo img {
  width: 100%;
  margin-top: 100px;
  margin-bottom: 25px;
}

.login-wrap::placeholder {
  color: #e86ea9;
  opacity: 0.8;
}

.login-button {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 10px;
}

.login-wrap .button {
  width: 40%;
}
