.upload-wrapper {
  height: 100vh;
  margin: 0 10px;
  margin-top: 10px;
}
.upload-text textarea {
  outline-style: none;
  border: none;
  width: 100%;
  height: 100px;
}

.upload-wrapper .uploadPicture {
  width: 180px !important;
  height: 240px;
  object-fit: cover;
}

.uploadPictureContainer {
  width: fit-content !important;
}

.upload-wrapper textarea::placeholder {
  color: #848484;
}

.upload-wrapper .confirm {
  position: fixed;
  height: 24px;
  top: 8px;
  right: 15px;
  z-index: 99;
  background-color: #e86ea9;
  color: white;
  font-weight: 900;
  padding: 5px 10px;
  border-radius: 10%;
}
