.button {
  margin: 0 auto;
  display: block;
  width: 200px;
  height: 40px;
  padding: 10px 25px;
  line-height: 20px;
  font-size: 20px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  background: linear-gradient(90deg, #8f79b8, #e86ea9);
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}

.button:active {
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.error {
  color: red;
  text-align: center;
  margin-bottom: 20px;
}

.background {
  background-color: #f5f5f9;
}

.profilePhoto {
  width: 48px;
  height: 48px;
  object-fit: cover;
  border-radius: 50%;
}

body {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-sex-age {
  width: 40px;

  height: 15px;
  border-radius: 20px;
  line-height: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.profile-sex-age span {
  font-size: 12px;
  color: white;
}

.profileSex-wrapper .female {
  background-color: #f052c0;
}

.profileSex-wrapper .male {
  background-color: #007fc9;
}

.profile-username {
  cursor: pointer;
}
