.page-wrapper .page-background {
  position: relative;
  overflow: hidden;
}

.page-wrapper {
  margin-bottom: 60px;
}

.page-wrapper .page-background img {
  width: 100vw;
  height: 180px;
  object-fit: cover;
}

.page-wrapper .errorBox {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: error-in 0.5s ease forwards;
}

.page-wrapper .errorBox .error-close {
  background-color: coral;
  width: 20px;
  height: 20px;
  display: inline-block;
  line-height: 20px;
  text-align: center;
  border-radius: 50%;
  color: white;
  position: absolute;
  top: -10px;
  right: -10px;
}

@keyframes error-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes error-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.page-wrapper .errorBox .error-msg {
  border-radius: 15px;
  background-color: #ff1e1e;
  padding: 5px 15px;
  color: white;
  display: inline-block;
  line-height: inherit;
}

.page-background-uploader {
  width: 100vw;
  height: 180px;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
}

.page-wrapper .page-background span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: 900;
  font-size: 16px;
}

.page-middle {
  position: relative;
}

.page-middle-content {
  position: absolute;
  top: -50px;
  right: 20px;
}

.page-middle-top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.page-middle-top img {
  margin-left: 10px;
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
}

.page-middle-top span {
  color: white;
  font-weight: 900;
  text-shadow: 1px 1px black;
}

.page-middle-bottom {
  width: 300px;
  text-align: right;
  color: #848484;
}

.page-content-body {
  margin-top: 5px;
}
.page-content-body img {
  width: 90px;
  height: 90px;
  object-fit: cover;
}

.page-content-body .time {
  display: block;
  width: 90px;
  height: 90px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

.page-content-body .ellipsisText {
  align-self: start;
  margin-left: 8px;
  width: 180px;
  text-overflow: ellipsis;
  height: 22px;
  overflow: hidden;
  white-space: nowrap;
}

.page-content-body {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.page-content-empty {
  height: 100px;
}

.page-content-body-zoom {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  right: -100vw;
  background-color: black;
  z-index: 100;
  overflow: hidden;
}

.page-content-body-zoom .fullText {
  position: absolute;
  bottom: 0;
  z-index: 100;
  color: white;
  padding: 20px 10px;
  background-color: rgba(132, 132, 132, 0.8);
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.page-content-body-zoom .fullText p:nth-child(1) {
  overflow: hidden;
  width: 200px;
  text-overflow: ellipsis;
}

.heart-comment {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 20px;
  box-sizing: border-box;
}
.heart-comment img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.heart-comment span {
  margin-right: 10px;
}

.page-content-body-zoom .zoomPic {
  position: absolute !important;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  height: auto !important;
}

.page-content-body-zoom .more {
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 100;
  width: 30px;
  height: 30px;
}

.more-menue {
  position: absolute;
  width: 100%;
  height: 110px;
  background-color: #848484;
  bottom: -110px;
  left: 0;
  z-index: 100;
}

.delete-wrapper,
.cancel-wrapper {
  width: 100%;
  height: 50px;
  line-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
}

.delete-wrapper {
  background-color: red;
  color: white;
}
.cancel-wrapper {
  margin-top: 10px;
  background-color: white;
}

.personalPage-talk {
  position: fixed;
  z-index: 100;
  top: 10px;
}

.personalPage-chat {
  width: 40px;
}

.slide-up {
  animation: slide-up 0.3s ease forwards;
}

.slide-down {
  animation: slide-down 0.3s ease forwards;
}

@keyframes slide-in {
  100% {
    right: 0;
  }
}

@keyframes slide-up {
  0% {
    bottom: -110px;
  }
  100% {
    bottom: 0;
  }
}

@keyframes slide-down {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -110px;
  }
}
