.user-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.profileSex-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profileSex-wrapper .profile-username {
  margin-right: 10px;
}

.user-profile {
  margin-top: 40px;
}

.user-info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.user-profile img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.user-content .prefer-sex {
  margin-top: 40px;
  font-size: 20px;
  font-style: 900;
  background: linear-gradient(
    90deg,
    #ee1a23,
    #f8931c,
    #fff000,
    #06a54f,
    #313393,
    #90278f
  );
  background-clip: text;
  color: transparent;
}

.user-content .prefer-sex-img {
  margin-top: -30px;
  width: 120px;
}

.user-content .detail {
  margin: 0px 20px 20px 20px;
  padding: 5px 10px;
  border: 1px solid #848484;
  border-radius: 10px;
  box-shadow: 3px 3px 10px grey;
}

.buttons {
  margin-top: 60px;
}

.update-info,
.sign-out {
  margin-top: 20px !important;
  width: 80% !important;
}

.profile-username {
  cursor: default !important;
}
